<template>
  <div>
    <el-row class="mart10">
      <!-- 新闻内容 -->
      <el-card class="box-card">
        <el-col>
          <div class="ql-editor">
            <el-input class="input1" v-model="data.path" type="text" placeholder="请输入hdfs路径" />
            <el-upload class="upload-demo" ref="upload" :action="'http://localhost:8080/uploadFile?hdfsPath=' + data.path"
              :show-file-list="false" :limit="1" :on-preview="handlePreview" :on-remove="handleRemove"
              :file-list="fileList" :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
              <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
            </el-upload>
            <div style="margin-top: 20px;">
              <el-input class="input1" v-model="data.path1" type="text" placeholder="请输入hdfs路径" />
              <el-button slot="trigger" size="small" type="primary" style="margin-left: 20px;"><a target="downloadFile"
                  :href="'http://localhost:8080/downloadFile?hdfsPath=' + data.path1" download>下载</a>
              </el-button>
            </div>
            <!-- 写入Excel -->
            <div style="margin-top: 20px;">
              <el-input class="input1" v-model="readFilePath" placeholder="请输入Excel文件路径"></el-input>
              <el-button slot="trigger" size="small" type="primary" @click="readExcel"
                style="margin-left: 20px;">读取Excel</el-button>
              <el-alert v-if="readResult" title="Excel读取结果" :type="readSuccess ? 'success' : 'error'">{{ readResult
              }}</el-alert>
            </div>


            <div style="margin-top: 20px;">
              <el-input class="input1" v-model="data.input.value" type="text" placeholder="请输入换算数据" />
              <el-select v-model="data.input.unit" style="margin-left: 20px;">
                <el-option v-for="option in data.options" :key="option.value" :label="option.label"
                  :value="option.value"></el-option>
              </el-select>
              <el-button slot="trigger" size="small" type="primary" style="margin-left: 20px;" @click="convert">数据转换</el-button>
            </div>
            <p v-if="output">{{ input.value }} {{ input.unit }} = {{ output }}</p>
          </div>
        </el-col>
      </el-card>
    </el-row>
  </div>
</template>

<script>
import globalTitle from '../globalTitle.vue'

import axios from 'axios';
export default {
  name: 'definition',
  components: {
    globalTitle
  },
  data() {
    return {
      data: {
        readFilePath: '',
        path: '',
        path1: '',
        input: { value: null, unit: 'km' },
        output: null,
        options: [
          { label: 'Kilometers', value: 'km' },
          { label: 'Meters', value: 'm' },
        ]
      }
    }
  },
  created() { },
  mounted() {
    this.getTitle()
  },
  watch: {
    menuList(n, o) {
      this.cont = this.menuList[0].name
      this.getnews(this.menuList[0]) //改变对象，菜单栏的东西要写到监听属性里边
    }
  },
  methods: {
    httpRequest(param) {
      this.data.file = param.file
    },
    /* 提交修改*/
    submitUpload() {
      this.$refs.upload.submit()
    },
    convert() {
      axios.get(`http://localhost:8080/convert/${this.data.input.value}/${this.data.input.unit}`)
        .then(response => this.data.output = response.data.output)
        .catch(error => console.log(error));
    },
    // 读取Excel
    readExcel() {
      axios.post('http://localhost:8080/read-excel', { filePath: this.data.readFilePath }).then(response => {
        this.readResult = response.data;
        this.readSuccess = true;
      }).catch(error => {
        this.readResult = error.response.data.message;
        this.readSuccess = false;
      });
    },

  }
}
</script>
